a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

ul, li {
  list-style: none; }

h1 {
  display: none; }

.header {
  min-height: 825px;
  background-image: url("../img/bikebg-desktop@1x.jpg"), linear-gradient(#0d0d0d, #262626);
  background-position: center;
  background-repeat: no-repeat;
  font-family: "Open Sans", "Arial", sans-serif; }

.container {
  max-width: 909px;
  padding: 0 10px;
  margin: 0 auto; }

.header__menu {
  padding-top: 21px;
  max-height: 62px;
  margin-left: 33px;
  margin-right: 43px; }

.header__menu-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0; }

.header__menu-item {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  text-align: center;
  color: #ffffff;
  opacity: 0.7; }

.header__inner {
  margin-top: 265px;
  margin-left: 11px;
  width: 395px; }

.header__inner-logo {
  display: block;
  margin-bottom: 31px; }

.header__inner-head {
  margin: 0;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px;
  color: #ffffff; }

.header__inner-text {
  margin: 0;
  margin-top: 24px;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
  opacity: 0.8; }

.header__inner-button {
  display: none; }

@media (max-width: 1023px) {
  .header {
    min-height: 340px;
    background-image: url("../img/bikebg-tablet@1x.jpg"), linear-gradient(#0d0d0d, #262626); }
  .container {
    max-width: 1023px;
    padding-left: 21px;
    padding-right: 0;
    margin: 0; }
  .header__menu {
    display: none; }
  .header__inner {
    display: flex;
    flex-direction: column;
    margin-top: 0;
    margin-left: 0;
    width: auto; }
  .header__inner-logo {
    padding-top: 25px;
    margin-bottom: 88px; }
  .header__inner-button {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    background-color: #0AD9C6;
    cursor: pointer; }
  .header__inner-block {
    display: block;
    width: 23px;
    height: 1px;
    background-color: #000000;
    margin-top: 25px;
    margin-left: 13.75px; }
    .header__inner-block::before {
      content: "";
      position: absolute;
      display: block;
      width: 23px;
      height: 1px;
      background-color: #000000;
      top: 19px; }
    .header__inner-block::after {
      content: "";
      position: absolute;
      display: block;
      width: 23px;
      height: 1px;
      background-color: #000000;
      top: 31px; }
  .header__inner-wrapper {
    display: flex;
    justify-content: space-between; }
  .header__inner-head {
    font-size: 30px;
    line-height: 42px;
    text-align: center; }
  .header__inner-text {
    display: block;
    align-self: center;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    width: 65%; } }

@media (max-width: 767px) {
  .header {
    position: relative;
    min-height: 390px;
    min-width: 320px;
    background-image: url("../img/bikebg-mobile@1x.jpg"), linear-gradient(#0d0d0d, #262626); }
  .container {
    padding-left: 16px; }
  .header__menu {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    background-color: #ffffff;
    max-height: 500px;
    padding: 0;
    padding-top: 100px;
    width: 100%;
    z-index: 9; }
    .header__menu--active {
      display: block; }
  .header__menu-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap; }
  .header__menu-list li {
    margin-bottom: 30px; }
    .header__menu-list li:last-child {
      margin-bottom: 0; }
  .header__menu-item {
    display: flex;
    height: 60px;
    width: 100%;
    background-color: #f6f6f6;
    font-size: 20px;
    line-height: 28px;
    color: #48494d;
    padding-left: 16px;
    text-align: left;
    align-items: center;
    position: relative; }
    .header__menu-item::after {
      position: absolute;
      content: "";
      width: 25px;
      height: 25px;
      background: url("../img/arrow.svg") center no-repeat;
      left: calc(100% - 34px); }
  .header__inner-text {
    width: 95%; }
  .header__inner-button--active {
    background-color: #ffffff;
    z-index: 9;
    background: url("../img/cross.svg") center no-repeat; }
  .header__inner-block--active {
    display: none; }
    .header__inner-block--active::after, .header__inner-block--active::before {
      display: none; } }

main {
  position: relative; }

.form {
  font-family: "Open Sans", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 20px;
  max-width: 327px;
  position: absolute;
  left: 58%;
  top: -550px; }

.form input {
  padding-left: 30px;
  font-weight: normal;
  height: 48px;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  font-size: 15px;
  line-height: 24px;
  margin-bottom: 20px; }
  .form input::placeholder {
    font-weight: normal;
    font-size: 15px;
    color: #1c1c1c;
    opacity: 0.5; }

.form__head {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  color: #ffffff; }

.form__submit {
  background-color: #0AD9C6;
  height: 51px;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  text-align: center;
  color: #000000; }

@media (max-width: 1023px) {
  .form {
    max-width: 41.8%;
    margin: 0 auto;
    position: relative;
    left: 0;
    top: 0; }
  .form__head {
    color: #000000;
    font-size: 18px; } }

@media (max-width: 767px) {
  .form {
    margin-top: 70px;
    max-width: 90%; } }

.address-container {
  max-width: 957px;
  margin: 0 auto; }

.address__inner {
  font-family: "Open Sans", "Arial", sans-serif;
  text-align: center; }

.address__head {
  font-size: 30px;
  line-height: 30px;
  margin: 0;
  color: #1f1f1f;
  font-weight: 600; }

.address__text {
  margin-top: 30px;
  margin-bottom: 41px;
  font-weight: 400;
  font-size: 17px;
  line-height: 31px;
  color: rgba(0, 0, 0, 0.7);
  width: 555px;
  margin-left: auto;
  margin-right: auto; }

.address__map {
  background: url("../img/map-desktop@1x.jpg") center no-repeat; }

.footer {
  margin: 39px auto 98px auto;
  display: flex;
  justify-content: space-between;
  width: 548px;
  font-family: "Open Sans", "Arial", sans-serif; }

.footer__mail {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 22px; }

.footer__mail a {
  color: rgba(0, 0, 0, 0.5); }

.footer__icon-mail {
  margin-right: 15px; }

.footer__tel {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 32px; }

.footer__tel a {
  color: rgba(0, 0, 0, 0.5); }

.footer__icon-phone {
  margin-right: 15.5px;
  background-color: #ffffff; }
